import sinon from 'sinon';
import { IStore } from '../../../src/redux/store/types';
import { mapStateToProps } from '../../../src/components/app-header';
import * as  selectors from "../../../src/redux/selectors/selectors";

//import  { getToDoCount, getDoneCount } from "../../../src/redux/selectors/selectors";


const getToDoItemValueStub = sinon.stub(selectors, 'getToDoCount').returns(3);
const getToDoneItemValueStub = sinon.stub(selectors, 'getDoneCount').returns(0);

describe('src/components/AppHeader', () => {

    beforeEach(() => {
        sinon.resetHistory();
    });

    it('mapStateToProps', () => {
        //Given
        const store = {} as IStore;
        const expected = {
             getToDoItemValueStub,
             getToDoneItemValueStub
        };

        //When
        const actual = mapStateToProps(store);

        //Then
        expect(actual).toEqual(expected);

    })
});